import axios from "axios";

// Establece la clave API en la cabecera por defecto para todas las solicitudes
axios.defaults.headers.common["x-api-key"] = "live_4GKnCRc94j14SLqeuAx178No8qXxKhrRbjCZ6r6XXS55HTVdY9VXpePMhiK1EhuN";

// Funcin para hacer una peticin HTTP GET a la API de The Cat API para obtener la coleccin de razas
async function fetchBreeds() {
  try {
    const response = await axios.get('https://api.thecatapi.com/v1/breeds');
    return response.data; // Devuelve el array de razas
  } catch (error) {
    console.error('Error:', error);
    throw error; // Propaga el error
  }
}

// Funcin para hacer una peticin HTTP GET a la API de The Cat API para obtener informacin completa sobre un gato por identificador de raza
async function fetchCatByBreed(breedId) {
  try {
    const response = await axios.get(`https://api.thecatapi.com/v1/images/search?breed_ids=${breedId}`);
    const catData = response.data[0]; // Tomamos el primer resultado asumiendo que es el nico
    return {
      breedName: catData.breeds[0].name,
      description: catData.breeds[0].description,
      temperament: catData.breeds[0].temperament,
      imageUrl: catData.url
    };
  } catch (error) {
    console.error('Error:', error);
    throw error; // Propaga el error
  }
}


fetchBreeds()
  .then(breeds => {
    // rellena un select
    const select = document.querySelector('.breed-select');
    breeds.forEach(breed => {
      const option = document.createElement('option');
      option.value = breed.id;
      option.textContent = breed.name;
      select.appendChild(option);
    });

    // Maneja el cambio en el select de razas
    select.addEventListener('change', async (event) => {
      const breedId = event.target.value;
      try {
        const catData = await fetchCatByBreed(breedId);
        // Mostra informacin del gato en el bloque .cat-info
        const catInfo = document.querySelector('.cat-info');
        catInfo.innerHTML = `
          <h2>${catData.breedName}</h2>
          <p>Description: ${catData.description}</p>
          <p>Temperament: ${catData.temperament}</p>
          <img src="${catData.imageUrl}" alt="Cat Image">
        `;
      } catch (error) {
        console.error('Error al obtener la informacin del gato:', error);
      }
    });
  })
  .catch(error => {
    // Maneja errores de la peticin
    console.error('Error al obtener las razas de gatos:', error);
  });

export { fetchBreeds, fetchCatByBreed };